<template>
	<view>
		<h>停车场编号为：{{pid}}</h><br>
		<h>车位编号为：{{name}}</h><br>
		<h>请选择你要预定的时间</h><br>
		<view class="example-body">
			<uni-datetime-picker
				v-model="datetimerange"
				type="datetimerange"
				start="2021-3-20 12:00:00"
				end="2021-6-20 20:00:00"
				rangeSeparator="至"
			/>
		</view>
		
		<view >{{"你选择的初始时间是："+datetimerange[0]}}</view>
		<view>{{"你选择的结束时间是："+datetimerange[1]}}</view>
		<view>{{"价格是:"+price}}</view>
		<button @click="pay">支付</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				name:"",
				pid:"",
				datetimerange: ["2021-03-20 20:10:10", "2021-03-20 20:10:10"]
			};
		},
		computed:{
			price:function(){
				let start=new Date(this.datetimerange[0]);
				let end=new Date(this.datetimerange[1])
				let time=(end.getTime()-start.getTime())/1000/3600
				return time*5.5;
			}
		},
		onLoad(e) {
			this.name=e.name,
			this.pid=e.pid
		},
		methods:{
			pay(){
				console.log("进行支付，调用接口，支付成功后修改数据库数据")
			}
		}
			
	}
</script>

